@import url('https://fonts.googleapis.com/css2?family=Lexend+Deca&display=swap');

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Lexend Deca'
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #b998dc !important;
  width: 100%;
  /* min-width: 1000px; */
}

* {
  font-family: "Lexend Deca";
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.newbody {
  width: 50%;
  display: flex;
  flex-direction: column;
  margin-left: 25%;
  margin-right: 25%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 30px;
  background-color: #FCF0CC;
}

.bg {
  background-color: #b998dc;
  padding-top: 10%;
  padding-bottom: 3%;
}

.newbody-members {
  padding: 4%;
}

.newbody-header {
  background-color: #311451;
  color: #FFFFFF;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 30px 30px 0px 0px;
  font-weight: 700;
  font-size: 2.5rem;
  margin-top: 0;
}


/*  Header  */
.beats {
  color: #FFFFFF;
}

.heading {
  color: #FFFFFF;
  font-size: 50px;
  background-color: #311451;
  text-align: center;
  margin: 0;
  padding: 3%;
}

/*  Timeline  */
.container {
  min-height: 70vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #b998dc;
  /* margin: 0 !important; */
  /* margin-left: 0 !important;
  margin-right: 0 !important; */
}

.Collapsible {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 20px;
  overflow: hidden;

}

.Collapsible__trigger {
  font-weight: 700;
  font-size: 1.5rem;
  background-color: #311451;
  color: #FFFFFF;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px 20px 0 0;
}

.Collapsible__contentOuter {
  background-color: #FCF0CC;
}


.timeline {
  width: 100%;
  height: auto;
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
}

.timeline ul {
  list-style: none;
  padding-left: 0;
}

.timeline ul li {
  padding: 20px;
  margin-bottom: 20px;
}

.timeline ul li:last-child {
  margin-bottom: 0;
}

.timeline-content {
  /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */
  font-size: 20px;
  /* border-radius: 20px; */
  background-color: #311451;
  color: #FFFFFF;
  padding: 2%;
  font-size: 140%;
  /* width: 40%; */
}

@media only screen and (min-width: 768px) {
  .timeline::before {
    content: '';
    position: absolute;
    height: 100%;
    width: 4px;
    background-color: #FCF0CC;
    left: 50%;
    transform: translateX(-50%);
  }

  .timeline ul li {
    width: 50%;
    margin-bottom: 30px;
    position: relative;
  }

  .timeline ul li:nth-child(odd) {
    float: right;
    clear: left;
    transform: translateX(47px);
  }

  .timeline ul li:nth-child(even) {
    float: left;
    clear: right;
    transform: translateX(-46px);
  }

  .timeline ul li::after {
    content: '';
    position: absolute;
    height: 3px;
    width: 20px;
    background-color: #FCF0CC;
    border-radius: 50%;
    top: 0;
  }

  .timeline ul li:nth-child(odd)::after {
    transform: translate(50%, -50%);
    left: -27px;
  }

  .timeline ul li:nth-child(even)::after {
    transform: translate(50%, -50%);
    right: -4px;
  }

  .timeline ul li:hover:after {
    background-color: #311451;
  }

}

@media only screen and (max-width:1730px) {
