  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  background-color: #FCF0CC;
}

.lead1 div img {
  margin-top: 2%;
}

.lead1 div h1 {
  font-size: 32px;
}

.lead1 div h4 {

  font-size: 1.5rem;
}

.lead1-19>div {
