}

.lead1-19 img {
  width: 40%;
}

.lead1-19 h1 {
  font-size: 32px;
}

.lead1-19 h4 {
  font-size: 25px;
}

.lead1 {
  display: grid;
  /* flex-wrap: wrap; */
  /* gap: 2%; */
  column-gap: 20%;
  row-gap: 0.5%;
  grid-template-columns: 1fr 1fr 1fr;
  justify-content: center;
  width: 70%;
  margin: 0 auto;
  margin-top: 3%;
}

.lead1>div {
  text-align: center;
}

.lead1>div:last-child {
  grid-column: auto / span 3;
}

