  display: flex;
  /* flex-direction: column; */
  justify-content: center;
  align-items: center;
  width: 100%;
  gap: 15%;
  background-color: #FCF0CC;
}

.lead1-19 {
  display: grid;
  /* flex-direction: column; */
  /* justify-content: center; */
  /* margin: 0 auto; */
