  margin-top: 1%;
}

.mag-button {
  background-color: #FCF0CC;
  color: #311451;
  font-size: 2rem;
  font-weight: bold;
  display: inline-block;
  outline: none;
  border: none;
  padding: 1rem 4rem;
  border-radius: 40px;
  cursor: pointer;
  width: auto;
  margin-top: 1%;
  margin-bottom: 2%;
  transition: transform .6s;
}

.mag-button:hover {
  transform: scale(1.2);
}


